body img,
video {
  max-width: 100%;
}
body {
  background: brown;
  overflow-x: hidden;
}

p {
  font-size: 2.5rem;
  font-family: "Lusitana", serif;
  z-index: 5;
}

.blender {
  margin-inline: auto;
  display: flex;
  justify-content: center;
  min-height: 100vh;
  max-width: 40rem;

  .doughnut,
  .cup {
    position: absolute;
    visibility: hidden;
  }

  .doughnut-anim {
    visibility: visible;
    position: absolute;
    left: 1rem;
    top: 74rem;
    height: 34rem;
    width: 59rem;
    object-fit: cover;
    border-radius: 1000vw;
    animation: doughnut 6s linear;
  }
  @keyframes doughnut {
    from {
      transform: translateX(-80rem);
    }
    to {
      transform: translateX(0rem);
    }
  }
  .cup-anim {
    visibility: visible;
    position: absolute;
    right: 0;
    top: 95rem;
    height: 34rem;
    width: 37rem;
    object-fit: cover;
    object-position: -14rem 0;
    border-radius: 1000vw;
    z-index: 1;
    animation: cup 8s linear;
  }
  @keyframes cup {
    from {
      transform: translateX(50rem);
    }
    to {
      transform: translateX(0rem);
    }
  }
  .about-me {
    padding-top: 20rem;
    br {
      margin-top: 10rem;
    }

    p {
      position: relative;
      text-align: justify;
    }
    p:nth-of-type(1) {
      left: -15rem;
      background-color: #ffffff75;
      backdrop-filter: blur(5px);
      border-radius: 10px;
      padding: 2rem;
    }
    p:nth-of-type(3) {
      margin-top: -15rem;
      background: none;
    }
    p:nth-of-type(4) {
      left: 15rem;
      background-color: #ffffff75;
      backdrop-filter: blur(5px);
      border-radius: 10px;
      padding: 2rem;
    }

    .blender-observer::before {
      content: "";
      position: absolute;
      top: -2rem;
      left: -2rem;
      height: 29rem;
      width: 43rem;
      border-radius: 10px;
      background-color: #ffffff75;
      backdrop-filter: blur(5px);
      z-index: -1;
    }
    .ble,
    .nder {
      position: relative;
      transform: scale(0.7);
      visibility: hidden;
      z-index: 5;
    }
    .ble-anim {
      visibility: visible;
      top: -3rem;
      left: -5rem;
      animation: ble 3s linear;
    }
    @keyframes ble {
      from {
        transform: translateX(-200px) scale(0.7);
        opacity: 0;
      }
      to {
        transform: translateX(0px) scale(0.7);
        opacity: 1;
      }
    }

    .nder-anim {
      visibility: visible;
      top: -14rem;
      left: -5rem;
      animation: nder 3s linear;
    }
    @keyframes nder {
      from {
        transform: translateX(200px) scale(0.7);
        opacity: 0;
      }
      to {
        transform: translateX(0px) scale(0.7);
        opacity: 1;
      }
    }
  }
}
.nintrondo {
  position: relative;
  padding-bottom: 31rem;
  p {
    text-align: justify;
  }
  .tron-text {
    position: relative;
    max-width: 30rem;
    background-color: #ffffff75;
    backdrop-filter: blur(5px);
    border-radius: 10px;
    padding: 2rem;
    margin-inline: auto;
    top: -9rem;
  }
  .nintrondo-video {
    filter: grayscale(70%);
    width: 100vw;
  }
  .light-tron,
  .day-tron {
    position: absolute;
    visibility: hidden;
    height: 38rem;
    width: 41rem;
    object-fit: cover;
  }
  .dark-tron {
    position: absolute;
    visibility: hidden;
    height: 38rem;
    width: 100%;
    object-fit: cover;
  }
  .light-anim {
    right: 0;
    top: 67rem;
    visibility: visible;
    animation: light-tron 5s linear;
    animation-fill-mode: forwards;
  }
  @keyframes light-tron {
    from {
      transform: translateX(40rem);
      object-position: 0;
    }
    to {
      transform: translateX(0rem);
      object-position: -22rem;
    }
  }
  .day-anim {
    left: 0;
    top: 67rem;
    visibility: visible;
    animation: day-tron 5s linear;
    animation-fill-mode: forwards;
  }
  @keyframes day-tron {
    from {
      transform: translateX(-40rem);
      object-position: 0;
    }
    to {
      transform: translateX(0rem);
      object-position: -22rem;
    }
  }
  .dark-anim {
    left: 0;
    top: 106rem;
    visibility: visible;
    animation: dark-tron 7s linear;
    animation-fill-mode: forwards;
  }
  @keyframes dark-tron {
    from {
      transform: translateY(10rem);
      object-position: 0rem 10rem;
    }
    to {
      transform: translateY(0rem);
      object-position: 0rem -10rem;
    }
  }
}
.nova {
  position: relative;
  .nova-video::before {
    content: "";
    height: 10rem;
    width: 10rem;
    inset: 0;
    position: absolute;
    background: red;
  }
  p {
    position: relative;
    max-width: 30rem;
    background-color: #ffffff75;
    backdrop-filter: blur(5px);
    border-radius: 10px;
    padding: 2rem;
    margin-inline: auto;
    top: -9rem;
    transform: translateX(0rem);
    transition: 2s ease-in-out;
  }
  .nova-top-anim {
    transform: translateX(-15rem);
  }
  .nova-bot-anim {
    transform: translateX(15rem);
    z-index: 7;
  }
  .nova-normal,
  .nova-second,
  .nova-bots {
    position: absolute;
    visibility: hidden;
    opacity: 0;
    transition: opacity 2s ease-in-out;
  }
  .normal-anim {
    visibility: visible;
    left: 0rem;
    top: 68rem;
    height: 55rem;
    object-fit: cover;
    opacity: 1;
  }
  .second-anim {
    visibility: visible;
    left: 0rem;
    top: 68rem;
    height: 55rem;
    object-fit: cover;
    animation: secondlayer 20s alternate infinite;
  }
  @keyframes secondlayer {
    0% {
      opacity: 0%;
    }
    50% {
      opacity: 100%;
    }
    100% {
      opacity: 100%;
    }
  }
  .bots-anim {
    visibility: visible;
    left: 0rem;
    top: 68rem;
    height: 55rem;
    object-fit: cover;
    animation: thirdlayer 20s alternate infinite;
  }
  @keyframes thirdlayer {
    0% {
      opacity: 0%;
    }
    50% {
      opacity: 0%;
    }
    100% {
      opacity: 100%;
    }
  }
  .nova-trans {
    position: absolute;
    top: 64rem;
    height: 70rem;
    transform: translateX(-10rem);
    z-index: 6;
    transition: 2s ease-in-out;
  }
  .trans-anim {
    height: 83rem;
    transform: translateX(65rem);
  }
  .trans-anim:hover {
    height: 100rem;
    top: 53rem;
  }
}
.wire-one-anim {
  position: absolute;
  left: 44rem;
  top: 68rem;
  height: 55rem;
  object-fit: cover;
}
.wire-two-anim {
  position: absolute;
  right: 0rem;
  top: 68rem;
  height: 55rem;
  object-fit: cover;
}
.printing {
  position: relative;
  margin-bottom: -10rem;
  .tech-final {
    width: 82rem;
  }
  .model-aspect {
    position: absolute;
    top: 47rem;
    left: 10rem;
  }
  .tech-model {
    width: 60rem;
    height: 60rem;
    object-fit: cover;
    border-radius: 1000vw;
  }
  .printing-text {
    position: absolute;
    margin-inline: auto;
    top: -2.5rem;
    right: 0rem;
    margin-bottom: -10rem;
  }
  p {
    max-width: 30rem;
    background-color: #ffffff75;
    backdrop-filter: blur(5px);
    border-radius: 10px;
    padding: 2rem;
  }
}
